.add {
  margin-bottom: 40px;
  & > .add__button {
    margin-bottom: 10px;
    padding: 10px 18px;
    background-color: #101010;
    text-align: center;
    text-decoration: none;
    font-family: "OpenSans-Bold";
    font-size: 11px;
    line-height: 15px;
    border: 1px solid #0d1e4b;
    color: white;
    cursor: pointer;
  }
  .form {
    &.show {
      display: flex;
      opacity: 1;
    }
    display: none;
    transition: 0.3s;
    opacity: 0;
    margin-top: 10px;
    flex-direction: column;
    gap: 20px;
    & > .form__status {
      border: none;
      font-family: "OpenSans-Regular";
      font-size: 14px;
    }
    & > .form__name {
      height: 35px;
      padding-left: 5px;
      border: 1px solid rgba(0, 0, 0, 1);
      font-family: "OpenSans-Regular";
      font-size: 14px;
    }
    & > .form__email {
      height: 35px;
      padding-left: 5px;
      border: 1px solid rgba(0, 0, 0, 1);
      font-family: "OpenSans-Regular";
      font-size: 14px;
    }
    & > .form__text {
      height: 100px;
      padding-left: 5px;
      border: 1px solid rgba(0, 0, 0, 1);
      font-family: "OpenSans-Regular";
      font-size: 14px;
      resize: none;
    }
    & > .form__button {
      width: fit-content;
      margin-bottom: 10px;
      padding: 10px 18px;
      align-self: center;
      background-color: #101010;
      text-align: center;
      text-decoration: none;
      font-family: "OpenSans-Bold";
      font-size: 11px;
      line-height: 15px;
      border: 1px solid #0d1e4b;
      color: white;
      cursor: pointer;
    }
  }
}
