.consumer {
  width: 240px;
  padding-bottom: 10px;
  border: 1px solid rgba(218, 218, 218, 1);
  transition: 0.8s;
}
.consumer:hover {
  border: 1px solid rgba(0, 0, 0, 1);
}
.consumer__wrapper {
  max-width: 205px;
  margin: 15px auto;
}
.consumer__info {
  display: flex;
  flex-direction: column;
}
.consumer__name {
  font-family: "OpenSans-Bold";
  font-weight: 700;
  font-size: 17px;
  line-height: 23px;
}
.consumer__location {
  margin-bottom: 45px;
  font-family: "OpenSans-Regular";
  font-size: 12px;
  line-height: 17px;
  color: rgba(0, 0, 0, 0.41);
}
.consumer__button {
  width: 166px;
  margin-bottom: 10px;
  padding: 10px 18px;
  background-color: rgba(16, 16, 16, 1);
  text-align: center;
  text-decoration: none;
  font-family: "OpenSans-Bold";
  font-size: 11px;
  line-height: 15px;
  border: 1px solid rgba(13, 30, 75, 1);
  color: rgba(255, 255, 255, 1);
}
