.skeleton {
  margin-top: 20px;
  margin-bottom: 25px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.skeleton__content {
  width: 100%;
  display: flex;
  gap: 5px;
}
.skeleton__title {
  width: 30%;
  height: 2rem;
  border-radius: 3px;
  background: #eee;
  background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
  background-size: 200% 100%;
  animation: 1.5s shine linear infinite;
}
.skeleton__element {
  width: 20%;
  height: 2rem;
  border-radius: 3px;
  background: #eee;
  background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
  background-size: 200% 100%;
  animation: 1.5s shine linear infinite;
}

@keyframes shine {
  to {
    background-position-x: -200%;
  }
}

@media screen and (max-width: 800px) {
  .skeleton {
    align-items: center;
  }
  .skeleton__title {
    width: 50%;
  }
  .skeleton__content {
    flex-direction: column;
  }
  .skeleton__element {
    width: 100%;
  }
}

@media screen and (max-width: 480px) {
  .skeleton__title {
    width: 80%;
  }
}
