th {
  border: 1px solid rgba(0, 0, 0, 1) !important;
  line-height: 42px !important;
}
.info__empty_small {
  padding: 0 !important;
  min-width: 51px;
}
.info__empty_large {
  padding: 0 !important;
  min-width: 60px;
}
.info__empty_flood {
  height: 24px;
}
.info__name {
  width: 100%;
  padding: 12px 20px;
  text-align: left;
  font-family: "AlegreyaSansSC";
  font-weight: 600;
  font-size: 35px;
  line-height: 40px;
}
.info__details {
  font-family: "OpenSans-Regular";
  font-weight: 500;
  font-size: 18px;
  line-height: 17px;
}
.info__chat {
  width: 140px;
  border: 1px solid rgba(13, 30, 75, 1) !important;
  background-color: rgba(0, 0, 0, 1);
}
.info__invite {
  width: 200px;
  border: 1px solid rgba(13, 30, 75, 1) !important;
  background-color: rgba(0, 0, 0, 1);
}
.info__link {
  padding-top: 12px;
  padding-bottom: 12px;
  display: block;
  width: 100%;
  height: 10px;
  text-decoration: none;
  font-size: 11px;
  font-weight: 700;
  line-height: 15px;
  color: rgba(255, 255, 255, 1);
}

@media screen and (max-width: 992px) {
  .info__details {
    font-size: 12px;
  }
  .info__link {
    font-size: 9px;
  }
  .info__chat {
    width: auto;
  }
  .info__invite {
    width: auto;
  }
}
@media screen and (max-width: 800px) {
  .info {
    display: flex;
    justify-content: center;
  }
  .info__details {
    display: flex;
    flex-direction: column;
  }
  .info__empty_small {
    display: none;
  }
  .info__empty_large {
    display: none;
  }
  table {
    width: 100%;
  }
  .info__name {
    text-align: center;
  }
}
